.footer {
    @include flexAlignment();
    width: 100%;
    height: 63px;
    background-color: #131313;
    box-sizing: border-box;
    @media(max-width: 480px){
        height: max-content;
    }
    &__content {
        @include flexAlignment();
        @media(max-width: 1024px){
            padding: 10px 20px;
        }
        @media(max-width: 480px){
            flex-direction: column-reverse;
        }
    }
    &__contacts {
        @media(max-width: 480px){
            padding: 15px 0;
        }
    }
    &__address {
        color: #ffffff;
        font-family: "Ubuntu-Regular";
        font-size: 14px;
        line-height: 20px;
    }
    &__phone {
        color: #e45028;
        font-family: "Ubuntu-Bold";
        font-size: 14px;
        line-height: 20px;
    }
    &__social-button {
        @include flexAlignment();
        width: 144px;
        @media(max-width: 480px){
            padding-top: 15px;
        }
    }
    &__social-button > div, a {
        @include flexAlignment(flex, center, center);
        width: 27px;
        height: 24px;
    }
    &__social-button img {
        width: auto;
        height: 22px;
    }
    &__social-button-vk img {
        height: 14px;
    }
}
p {
    margin: 0;
}