@mixin flexAlignment($display: flex, $justifyContent: space-between, $alignItems: center) {
    display: $display;
    justify-content: $justifyContent;
    align-items: $alignItems;
}

@mixin textStyle($size, $color: #ffffff, $case: uppercase, $lineHeight: auto) {
    font-size: $size;
    color: $color;
    text-transform: $case;
    line-height: $lineHeight;
}