@font-face {
    font-family: "Ubuntu-Regular";
    src: url("../fonts/Ubuntu-Regular.ttf") format("truetype");
    font-style: normal;
    font-weight: normal;
}
@font-face {
    font-family: "Ubuntu-Bold";
    src: url("../fonts/Ubuntu-Bold.ttf") format("truetype");
    font-style: bold;
    font-weight: bold;
}
@font-face {
    font-family: "Lifehack-Medium";
    src: url("../fonts/Lifehack-Medium.woff") format("webopenfontformat");
    font-style: normal;
    font-weight: normal;
}
@font-face {
    font-family: "Gagalin-Regular";
    src: url("../fonts/Gagalin-Regular.ttf") format("truetype");
    font-style: normal;
    font-weight: normal;
}