section {
    height: 1000px;
    position: relative;
    font-family: "Ubuntu-Regular";
    @media (max-width: 1280px) {
        height: 768px;
    }
}
.vertical-nav {
    z-index: 150;
    position: absolute;
    top: 125px;
    right: 45px;
    @media (max-width: 1280px) {
        top: 90px;
    }
    @media (max-width: 480px) {
        right: 30px;
    }
    &__active {
        @include flexAlignment(flex, center, center);
        width: 16px;
        height: 16px;
        border-radius: 50%;
        border: 1px solid rgba(255, 255, 255, 0);
        &.active {
            border: 1px solid rgba(255, 255, 255, 1);
        }
    }
    & > div > a > div {
        width: 6px;
        height: 6px;
        background-color: #fff;
        border-radius: 50%;
    }
}
.banner {
    background-image: url(../img/background-first.png);
    width: 100%;
    // height: 1000px;
    background-size: cover;
    background-position: center;
    position: static;
    &__content {
        @include flexAlignment();
        height: 100%;
    }
    &__image {
        margin-right: 11px;
        padding-bottom: 75px;
    }
    &__image img {
        width: 450px;
    }
    &__subtitle {
        // font-family: "Ubuntu-Regular";
        font-size: 72px;
        line-height: 86px;
        text-align: left;
        color: $dark-yellow;
        text-transform: uppercase;
    }
    &__text-subtitle {
        // font-family: "Ubuntu-Regular";
        font-size: 65px;
        text-align: left;
        color: #fff;
    }
}

.about-us {
    background-image: url(../img/back-about-us.png);
    width: 100%;
    // height: 1000px;
    background-size: cover;
    background-position: right;
    @media (max-width: 1024px) {
        background-position: center;
    }
    &__content {
        @include flexAlignment(flex, center, center);
        flex-direction: column;
        padding: 225px 15% 0;
        @media (max-width: 1800px) {
            padding: 220px 8% 0;
        }
        @media (max-width: 1280px) {
            padding: 100px 5% 0;
        }
        @media (max-width: 1024px) {
            padding: 50px 4% 0;
        }
        @media (max-width: 460px) {
            padding: 25px 4% 0;
        }
    }
    &__subtitle {
        width: 550px;
        @include textStyle(95px, $dark-grey, uppercase, 65px);
        padding-bottom: 50px;
        @media (max-width: 1280px) {
            width: 800px;
        }
        @media (max-width: 1024px) {
            background-position: center;
            width: 100%;
            padding-left: 50px;
            font-size: 72px;
        }
        @media (max-width: 576px) {
            font-size: 50px;
        }
        @media (max-width: 460px) {
            font-size: 45px;
            padding: 0 0 15px 0;
            text-align: center;
        }
    }
    &__list {
        @include flexAlignment();
        width: 100%;
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__item {
        @include flexAlignment(flex, space-between, center);
        flex-direction: column;
        box-sizing: border-box;
        width: 100%;
        padding: 0 15px;
        @media (max-width: 768px) {
            @include flexAlignment(flex, space-between, flex-start);
            flex-direction: row;
            margin-bottom: 15px;
            padding: 0;
        }
        @media (max-width: 460px) {
            flex-direction: column;
            align-items: center;
        }
    }
    &__img {
        height: 120px;
        width: auto;
        margin-bottom: 40px;
        @media (max-width: 768px) {
            height: 120px;
            width: 25%;
            object-fit: contain;
            object-position: center;
        }
        @media (max-width: 460px) {
            width: 20%;
            height: auto;
            margin-bottom: 15px;
        }
    }
    &__item-content {
        @media (max-width: 768px) {
            width: 67%;
        }
        @media (max-width: 460px) {
            width: 100%;
        }
    }
    &__subtitle-item {
        @include textStyle(30px, $dark-grey, none, 1);
        text-align: center;
        margin-bottom: 40px;
        @media (max-width: 768px) {
            text-align: left;
            margin-bottom: 5px;
        }
        @media (max-width: 460px) {
            text-align: center;
        }
    }
    &__text {
        @include textStyle(18px, $dark-grey, none, 30px);
        text-align: center;
        max-width: 370px;
        @media (max-width: 768px) {
            text-align: left;
            max-width: 470px;
        }
        @media (max-width: 460px) {
            line-height: 20px;
            text-align: center;
        }
    }
}
.burgers-section {
    @include flexAlignment(flex, center, center);
    background: linear-gradient(#f9b33b, #f29f39, #eb8c38, #eb8c38,#f29f39, #f9b33b);
    position: relative;
    @media(max-width: 768px) {
        height: max-content;
        padding: 50px 0;
    }
    @media(max-width: 480px) {
        padding: 25px 0;
    }
    & .anchor {
        position: absolute;
        top: 0;
    }
}
.burgers {
    @include flexAlignment(flex, center, center);
    width: 980px;
    @media (max-width: 1024px) {
        width: 85%;
    }
    @media(max-width: 768px) {
        @include flexAlignment(flex, flex-start, center);
    }
    &__content {
        @include flexAlignment();
        @media (max-width: 1024px) {
            @include flexAlignment(flex, space-around, flex-start);
            position: relative;
        }
        @media(max-width: 768px) {
            flex-direction: column;
        }
    }
    &__image {
        width: 470px;
        height: 480px;
        position: relative;
        padding-top: 70px;
        box-sizing: border-box;
        @media (max-width: 1024px) {
            width: 305px;
            height: max-content;
            padding-top: 100px;
        }
        @media(max-width: 768px) {
            width: 100%;
            padding-top: 20px;
            @include flexAlignment(flex, center, flex-start);
        }
        @media(max-width: 576px) {
            padding-top: 70px;
        }
    }
    &__img {
        width: auto;
        height: 312px;
        @media (max-width: 1024px) {
            width: 300px;
            height: auto;
        }
        @media (max-width: 480px) {
            width: 250px;
            height: auto;
        }
    }
    &__composition {
        @include flexAlignment(flex, center, center);
        flex-direction: column;
        width: 90px;
        height: 90px;
        background: $orange;
        border-radius: 5px;
        position: absolute;
        left: 0;
        top: 0;
        @media (max-width: 480px) {
            width: 75px;
            height: 75px;
        }
        &-img {
            width: 46px;
            height: 45px;
        }
        &-text {
            @include textStyle(14px, #fff, uppercase, 1);
            text-align: center;
        }
    }
    &__calories {
        position: relative;
        color: rgba(255, 255, 255, 0.5);
        width: 57px;
        @media (max-width: 768px) {
            position: absolute;
            top: 5px;
            right: 15px;
        }
        &-img {
            width: 57px;
            height: 57px;
        }
        &-number {
            font-size: 20px;
            text-align: center;
            position: absolute;
            top: 30px;
            left: 6px;
        }
        &-text {
            font-size: 14px;
            text-align: center;
        }
    }
    &__description {
        min-height: 470px;
        @media (max-width: 1024px) {
            @include flexAlignment(flex, flex-start, flex-start);
            flex-direction: column;
            width: 305px;
            min-height: max-content;
        }
        @media (max-width: 768px) {
            width: 100%;
            align-items: center;
        }
        &-name {
            @include textStyle(72px, #fff, uppercase, 72px);
            @media (max-width: 1024px) {
                @include textStyle(50px, #fff, uppercase, 50px);
            }
            @media (max-width: 768px) {
                font-size: 35px;
                text-align: center;
            }
            @media (max-width: 480px) {
                line-height: 40px;
            }
        }
        &-text {
            @include textStyle(14px, #fff, none, 26px);
            padding-top: 25px;
            @media (max-width: 1024px) {
                max-width: 300px;
                padding-right: 25px;
                overflow: hidden;
            }
            @media (max-width: 768px) {
                max-width: 100%;
                padding: 20px 10px 80px;
            }
            @media (max-width: 480px) {
                line-height: 18px;
                padding-bottom: 90px;
            }
        }
        &-price {
            @include flexAlignment(flex, flex-start, flex-end);
            padding-top: 50px;
            @media (max-width: 1024px) {
                position: absolute;
                bottom: 0;
                right: 20px;
            }
            @media (max-width: 480px) {
                justify-content: center;
                flex-wrap: wrap;
            }
        }
        &-count {
            font-size: 72px;
            line-height: 53px;
            color: $color-button;
            @media (max-width: 576px) {
                letter-spacing: -4px;
            }
            @media (max-width: 480px) {
                font-size: 50px;
                letter-spacing: 0px;
            }
        }
        &-currency {
            @include textStyle(30px, $color-button, uppercase, 26px);
            @media (max-width: 480px) {
                @include textStyle(20px, $color-button, uppercase, 32px);
            }
        }
        &-button-link {
            text-decoration: none;
            outline: none;
        }
        &-button-order {
            width: 100px;
            height: 38px;
            text-transform: uppercase;
            text-align: center;
            padding-top: 12px;
            margin-left: 20px;
            font-size: 13px;
            color: #ffffff;
            background-color: $color-button;
            border-radius: 5px;
            box-sizing: border-box;
        }
    }
}
/////////////////////////////slider
.slick-list {
    overflow: hidden;
    @media (max-width: 1024px) {
        height: 420px !important;
    }
    @media (max-width: 768px) {
        height: max-content !important;
    }
}
.slider-item {
    display: flex !important;
}
.slick-track{
    display: flex;
}
