.team {
    background-image: url(../img/back-team.png);
    width: 100%;
    background-size: cover;
    background-position: center;
    @media (max-width: 1024px){
        background-position: left;
    }
    @media(max-width: 768px) {
        min-height: 360px;
        height: max-content;
        padding-bottom: 80px;
    }
    &:after {
        content: '';
        width: 50%;
        height: 100%;
        background:rgba(36, 36, 36, 0.9);
        position: absolute;
        top: 0;
        right: 0;
        @media (max-width: 1024px){
            width: 75%;
        }
        @media (max-width: 768px){
            width: 100%;
        }
    }
    &__content {
        position: absolute;
        top: 123px;
        left: calc(50% + 123px);
        z-index: 5;
        @media (max-width: 1280px) {
            top: 80px;
            left: calc(50% + 50px);
        }
        @media (max-width: 1024px){
            left: calc(25% + 50px);
        }
        @media (max-width: 768px){
            position: relative;
            height: max-content;
            top: 50px;
            left: 50px;
        }
        @media (max-width: 460px){
            top: 25px;
            left: 4%;
        }
    }
    &__subtitle {
        @include textStyle(72px, $dark-yellow, uppercase, 56px);
        margin-bottom: 70px;
        @media (max-width: 1280px) {
            margin-bottom: 50px;
        }
        @media (max-width: 768px){
            margin-bottom: 25px;
        }
        @media (max-width: 576px){
            font-size: 50px;
        }
    }
    &__list {
        list-style: none;
    }
    &__name {
        @include textStyle(14px, #ffffff, uppercase, 26px);
        margin: 28px 0 15px;
    }
    &__about-crewman {
        @include flexAlignment(flex, flex-start, flex-start);
        margin-left: 90px;
        display: none;
        @media (max-width: 1280px) {
            margin-left: 50px;
        }
        @media (max-width: 460px){
            margin-left: 4%;
        }
    }
    &__img {
        width: 98px;
        height: 98px;
        object-fit: cover;
        object-position: center;
        @media (max-width: 1280px) {
            width: 70px;
            height: 70px;
        }
    }
    &__data-crewman {
        margin-left: 70px;
        padding-right: 50px;
        @media (max-width: 1440px) {
            margin-left: 40px;
        }
        @media (max-width: 460px) {
            margin-left: 20px;
        }
    }
    &__position {
        @include textStyle(14px, #ffffff, none, 14px);
        font-family: "Ubuntu-Bold";
    }
    &__text {
        @include textStyle(14px, #ffffff, none, 26px);
        margin-top: 12px;
        @media (max-width: 1280px) {
            margin-top: 5px;
            line-height: 18px;
        }
    }
}
.fa-caret-up,
.fa-caret-down {
    width: 10px;
    height: 7px;
    color: $dark-yellow;
    margin-right: 7px;
}
.fa-caret-up {
    display: none !important;
}
.team-active > .team__about-crewman {
    display: flex !important;
}
.team-active > .team__name {
    color: $dark-yellow;
}
.team-active > p > .fa-caret-down {
    display: none !important;
}
.team-active > p > .fa-caret-up {
    display: inline-block !important;
}

.menu {
    background-image: url(../img/back-burger.png);
    width: 100%;
    // height: 1000px;
    background-size: cover;
    background-position: center;
    position: relative;
    @media(max-width: 576px) {
        @include flexAlignment(flex, flex-start, flex-end);
    }
    &__title {
        @include textStyle(72px, #fff, uppercase, 1);
        position: absolute;
        top: 145px;
        left: 190px;
        z-index: 1;
        @media (max-width: 768px) {
            top: 50px;
            left: 50px;
        }
        @media (max-width: 576px) {
            font-size: 50px;
        }
    }
    &__list {
        list-style: none;
        display: flex;
        justify-content: flex-end;
        height: inherit;
        position: relative;
        z-index: 2;
        @media (max-width: 576px) {
            flex-direction: column;
            align-items: flex-end;
            justify-content: flex-end;
            height: 768px;
            width: 100%;
        }
        // @media (max-width: 576px) {
        //     -webkit-transform: rotate(90deg);
        //     -moz-transform: rotate(90deg);
        //     -o-transform: rotate(90deg);
        //     transform: rotate(90deg);
        //     width: 768px;
        //     position: absolute;
        //     bottom: 96px;
        //     left: -96px;
        // }
    }
    &__item {
        display: flex;
        justify-content: flex-end;
        max-width: 33.33%;
        height: inherit;
        @media (max-width: 1280px) {
            max-width: 50%;
        }
        @media (max-width: 768px) {
            max-width: 100%;
        }
        @media (max-width: 576px) {
            @include flexAlignment(flex, space-evenly, center);
            max-height: 85px;
            width: 100%;
        }
    }
    &__button {
        position: relative;
        @include flexAlignment(flex, center, center);
        width: 85px;
        height: inherit;
        cursor: pointer;
         @media (max-width: 576px) {
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -o-transform: rotate(90deg);
            transform: rotate(90deg);
        }
    }
    &__img {
        width: 85px;
        height: inherit;
        object-fit: cover;
        // @media (max-width: 576px) {
        //     -webkit-transform: rotate(270deg);
        //     -moz-transform: rotate(270deg);
        //     -o-transform: rotate(270deg);
        //     transform: rotate(270deg);
        // }
    }
    &__subtitle {
        @include textStyle(40px, #fff, uppercase, 20px);
        position: absolute;
        width: max-content;
        -webkit-transform: rotate(270deg);
        -moz-transform: rotate(270deg);
        -o-transform: rotate(270deg);
        transform: rotate(270deg);
        @media (max-width: 576px) {
            font-size: 32px;
        }
    }
    &__text {
        @include textStyle(14px, #fff, none, 24px);
        box-sizing: border-box;
        background: rgba(51, 51, 51, 0.9);
        width: 0px;
        transition: width 0.3s;
        padding: 0px;
        display: none;
        @media (max-width: 576px) {
            width: 100%;
            height: 0px;
            transition: height 0.3s;
        }
    }
    &__item.active > .menu__text{
        padding: 65px 60px 50px 40px;
        width: 100%;
        transition: width 0.3s;
        display: flex;
        overflow: hidden;
        box-sizing: border-box;
        @media (max-width: 576px) {
            height: 100%;
            transition: height 0.3s;
        }
        @media (max-width: 480px) {
            padding: 15px 60px 15px 10px;
        }
    }
    &__item.active {
        @media (max-width: 576px) {
            max-height: 598px;
        }
    }
    &__item.active > .menu__button {
        @media (max-width: 576px) {
            -webkit-transform: rotate(270deg);
            -moz-transform: rotate(270deg);
            -o-transform: rotate(270deg);
            transform: rotate(0deg);
            transition: transform 0.3s;
            height: 598px;
        }
    }
    &__item.active > .menu__button > .menu__subtitle{
        color: $dark-yellow;
    }
}

.quotes {
    background-image: url(../img/background-photo.png);
    width: 100%;
    background-size: cover;
    background-position: center;
    box-sizing: border-box;
    @media(max-width: 576px){
        height: 576px;
    }
    &__modal-window {
        z-index: 100;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(47, 50, 52, 0.9);
    }
    &__modal-window-content {
        @include flexAlignment(flex, center, center);
        position: relative;
    }
    &__content {
        position: relative;
        width: 460px;
        height: max-content;
        padding: 30px 20px;
        background: #fff;
        border-radius: 5px;
        @media (max-width: 576px){
            width: 90%;
        }
    }
    &__autor {
        @include textStyle(14px, #444444, none, 20px);
        width: 135px;
    }
    &__text {
        @include textStyle(14px, #444444, none, 20px);
        margin-top: 5px;
    }
    &__close {
        position: absolute;
        top: 20px;
        right: 20px;
    }
}

.delivery {
    @include flexAlignment(flex, center, center);
    background-image: url(../img/back-forth.png);
    width: 100%;
    // height: 1000px;
    background-size: cover;
    background-position: center;
    position: relative;
    & .anchor {
        position: absolute;
        top: 0;
    }
    &__content {
        height: max-content;
    }
    &__subtitle {
        @include textStyle(72px, $dark-yellow, uppercase, 56px);
        text-align: center;
        margin-bottom: 75px;
    }
    &__form {
        @include flexAlignment(flex, space-between, flex-start);
    }
    &__contacts-data,
    &__order-details {
        width: 445px;
    }
    label {
        @include textStyle(14px, #fff, none, auto);
    }
    input {
        margin: 20px 0;
        height: 27px;
        border: 1px solid #686868;
        border-radius: 5px;
        box-sizing: border-box;
        outline: none;
    }
    .name-phone {
        display: flex;
        justify-content: space-between;
        & input {
            width: 215px;
        }
    }
    input#street {
        width: 100%;
    }
    .address-block {
        @include flexAlignment(flex, space-between, flex-start);
        width: inherit;
        & input {
            width: 100px;
        }
    }
    form textarea {
        margin: 20px 0 0;
        width: 100%;
        height: 105px;
        border: 1px solid #686868;
        border-radius: 5px;
        box-sizing: border-box;
        outline: none;
    }
    .payment {
        margin-top: 10px;
        input {
            margin: 20px 10px 10px 0;
            height: auto;
        }
        label {
            margin-right: 15px;
        }
    }
    .submit-order {
        width: 100px;
        height: 38px;
        @include textStyle(13px, #fff, uppercase, 1);
        margin-top: 20px;
        background-color: $color-button;
        border: none;
        border-radius: 5px;
        box-sizing: border-box;
        outline: none;
        cursor: pointer;
    }
    .reset {
        @include textStyle(14px, #fff, capitalize, 1);
        margin-left: 15px;
        background: none;
        border: none;
        outline: none;
        cursor: pointer;
    }
}

.map {
    background-image: url(../img/background-map.png);
    width: 100%;
    background-size: cover;
    background-position: center;
    @media (max-width: 768px){
        background-image: url(../img/background-map-small.png);
        height: 460px;
        background-size: 150%;
    }
}