.wrapper {
    width: 940px;
    margin: 0 auto;
}
.header {
    @include flexAlignment();
    font-family: "Ubuntu-Regular";
    position: absolute;
    top: 0;
    left: calc((100% - 940px) / 2);
    &__logo {
        margin-top: 10px;
        width: 101px;
        height: 79px;
    }
    &__block {
        @include flexAlignment();
        width: 660px;
    }
    &__nav {
        @include flexAlignment();
        width: 534px;
        &-list {
            @include flexAlignment();
            list-style: none;
            width: 100%;
        }
        &-item:hover {
            text-decoration: underline;
        }
        &-item:after {
            content: ".";
            font-size: 25px;
            color: #ffffff;
            margin-left: 18px;
            position: absolute;
            bottom: 36px;
        }
        &-item:last-child:after {
            content: "";
        }
        &-item-link {
            font-size: 13px;
            color: #ffffff;
            text-decoration: none;
            text-transform: uppercase;
        }
    }
    &__button-link {
        text-decoration: none;
    }
    &__button-order {
        width: 100px;
        height: 38px;
        text-transform: uppercase;
        text-align: center;
        padding-top: 12px;
        font-size: 13px;
        color: #ffffff;
        background-color: $color-button;
        border-radius: 5px;
        box-sizing: border-box;
    }
    
}